<template>
  <div :class="isWidth ? 'App_all_1' : 'App_all_2'">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name:"App",
  data() {
    return {
      screenWidth: document.body.offsetWidth,
      isWidth: false,
    }
  },
  // 定义一个方法
  methods: {
    setClient(){
      if (this.screenWidth < 1400) {
        this.isWidth = false
      } else {
        this.isWidth = true
      }
    }
  },
  mounted() {
    this.setClient();
  },
}
</script>

<style scoped>
  .App_all_1{
    width: 100%;
    height: 100%;
  }
  .App_all_2{
    width: 1400px;
    height: 100%;
  }
</style>